import { Select } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { LogCliService } from "../../../services/adminService";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import './logCli.css';

export default function ManageLogCliIndexComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [options, setOptions] = useState([]);
    const [dataSource, setDataSource] = useState('');

    const loadData = useCallback(() => {
        setIsLoading(true);
        LogCliService.GetList((data: any) => {
            let row = data.data.data.map((data: any, index: number) => {
                return {
                    label: data,
                    value: data,
                    key: index
                }
            })
            setOptions(row);
        }, undefined, () => {
            setIsLoading(false);
        })
    }, []);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleChangeOption(data: string) {
        setIsLoading(true);
        LogCliService.GetDetail(data, (data: any) => {
            setDataSource(data.data.data);
        }, undefined, () => {
            setIsLoading(false);
        })
    }

    return (
        <MyBox
            title="Log hệ thống"
            isLoading={isLoading}>
            <Select onChange={handleChangeOption} options={options} />
            <Link to="/admin/logAction"> <MyButton value="Log thao tác người dùng" /></Link>
            <div>
                <pre className="terminal">{dataSource}</pre>
            </div>
        </MyBox>
    );
}
